/* LayoutStyles.css */

/* Reset some default styles */
* {
    margin: 0;
    padding: 0;
    padding-top: 5px;
    box-sizing: border-box;

  }
  
  body {
    font-family: Arial, sans-serif;
    background-color: #f0f2f5;
  }
  
  /* Main container */
  .main {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
  }
  
  /* Main layout */

  .layout {
    min-height: 550px;
    display: flex;
    max-width: 1200px;
    width: 100%;
    background-color: #fff;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  }
  
  /* Sidebar */
  .sidebar {
    flex: 1;
    padding: 20px;
    background-color: #2a2f3a;
    color: #fff;
  }
  
  .logo {
    text-align: center;
    margin-bottom: 20px;
  }
  
  .logo h1 {
    font-size: 24px;
  }
  
  .menu {
    margin-top: 20px;
  }
  
  .menu-item {
    display: flex;
    align-items: center;
    padding: 10px 20px;
    border-radius: 5px;
    margin-bottom: 10px;
    cursor: pointer;
  }
  
  .menu-item a {
    color: #fff;
    text-decoration: none;
    margin-left: 10px;
  }
  
  .menu-item.active {
    background-color: #1c1f25;
  }
  
  /* Content */
  .content {
    flex: 3;
    padding: 20px;
  }
  
   .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
  }
  
  .header-content {
    display: flex;
    align-items: center;
    cursor: pointer;
  }
  
  .header-content a {
    color: #333;
    text-decoration: none;
    margin-left: 10px;
  } 
  
  /* Style the Bell icon badge */
  .ant-badge-count {
    background-color: #1890ff;
    color: #fff;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
  }
  
  /* Body */
  .body {
    padding: 20px;
    background-color: #f0f2f5;
  }
  
  /* Logout button */
  .menu-item.logout {
    color: #fff;
    display: flex;
    align-items: center;
    padding: 10px 20px;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .menu-item.logout i {
    margin-right: 10px;
  }
  